//Слайдер
.screen {
  height: 500px;
  width: 800px;
}

 .imgslaider {
  width: 100%;;
}

.sim-slider {
  max-width: 1140px;
  min-width: 320px;
  margin: 20px auto;
  padding: 30px 50px;
  background-color: white;
}

/* General styles */
.sim-slider {
  position: relative;
}

.sim-slider-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
}

.sim-slider-element {
  width: 100%;
  transition: opacity 1s ease-in;
  opacity: 0;
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  display: block;
}

/* Navigation item styles */
div.sim-slider-arrow-left,
div.sim-slider-arrow-right {
  width: 42px;
  height: 50px;
  position: absolute;
  cursor: pointer;
  opacity: 0.6;
  z-index: 4;
}

div.sim-slider-arrow-left {
  top: 40%;
  display: block;
  background: url("../img/left.png") no-repeat;

  position: absolute;
  left: 70px;

}

div.sim-slider-arrow-right {
  top: 40%;
  display: block;
  background: url("../img/right.png") no-repeat;

  position: absolute;
  right: 70px;
}

div.sim-slider-arrow-left:hover {
  opacity: 1.0;
}

div.sim-slider-arrow-right:hover {
  opacity: 1.0;
}

div.sim-slider-dots {
  width: 100%;
  height: auto;
  position: absolute;
  left: 0;
  bottom: 50px;
  z-index: 3;
  text-align: center;
}

span.sim-dot {
  width: 20px;
  height: 20px;
  margin: 5px 7px;
  padding: 0;
  display: inline-block;
  background-color: none;
  border-radius: 20px;
  cursor: pointer;

  border-style: solid;
  border-color: #BBB;
  border-width: 1px;
}


// Каталог

.catalogue {
  display: flex;
  flex-direction: column;
  align-items: center;

}

.catalogue_photo {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: flex-start;
  height: 800px;
  width: 1170px;

}

.catalogue_title {
  height: 100px;
  margin-bottom: 20px;
  width: 50%;
  margin-left: 25% ;
  margin-right: 25%;
  text-align: center;
}


.catalogue_title p {
  font-size: 48px;
  color: #000;
  margin: 0px;
  padding: 0px;
}



.catalogue_name {
  height: 42px;
  width: 100%;
  padding-top: 0px;
  padding-bottom: 0px;
  margin-top: 0px;
  margin-bottom: 0px;
  position: absolute;
  bottom: 3px;
  border-width: 0px;

  background-color: rgba(48, 47, 46, 0.7);

  color: #fff;
  font-size: 24px;
  text-align: left;
  line-height: 42px;
  text-indent: 7%;
}

.catalogue_cat,
.catalogue_title,
.catalogue_photo {
