}

.cat_menu--item--call {
  height: 43px;
  width: 220px;

  margin-top: 6px;
  margin-bottom: 7px;
  text-align: center;

  border-radius: 40px;

  border-style: solid;
  border-width: 0px;

  background: url(../img/handset.png) no-repeat;
  background-position: 23px center;
  background-color: #6d5cad;

}

.cat_menu--item--call p {
  font-size: 15px;
  margin-top: 14px;
}

.cat_menu--item--call:hover,
.cat_menu--item--call:focus,
.cat_menu--item--call:active {
  background: url(../img/handset.png) no-repeat 23px center, linear-gradient(to left, #04accd, #75c29a);
  cursor: pointer;
}



// Форма Отправить вызов

// Кнопка закрыть
.call_close {
  width: 22px;
  height: 22px;
  font-size: 0;
  border: 0;
  background-color: #bcbcbc;
  cursor: pointer;

  position: absolute;
  top: 10px;
  right: 10px;

}

.call_close::before,
.call_close::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 2px;
  width: 19px;
  height: 2px;
  background-color: #000;
}

.call_close::before {
  transform: rotate(45deg);
}

.call_close::after {
  transform: rotate(-45deg);
}


// Вслплывающая форма

.form_closed {
  display: none;
}

.form_opened {
  display: block;
}


.window_call {
  position: fixed;
  z-index: 9999;

  top: 120px;
  left: 50%;

  width: 300px;
  margin-left: -230px;
  padding: 50px 80px;

